* {
  padding: 0;
  margin: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  min-height: 100%;
  position: relative;
  text-align: center;
  font-family: "arial";
  padding-bottom: 80px;
}

#root {
  margin-top: 50px;
  display: inline-block;
  /* Logs */
  /* Stats */
}
#root .log {
  font-size: 14px;
  background-color: black;
  color: white;
  padding-left: 5px;
  border: 3px solid black;
  text-align: left;
  line-height: 20px;
  height: 105px;
}
#root canvas {
  border: 3px solid black;
}
#root .row {
  padding: 5px 0px;
  display: -ms-flexbox;
  display: flex;
}
#root .row .img-container {
  cursor: default;
  -ms-flex-positive: 1;
      flex-grow: 1;
  position: relative;
}
#root .row .img-container .tooltip {
  z-index: 3;
  width: 100px;
  top: 28px;
  left: 0;
  position: absolute;
  display: none;
  background: white;
  border: 1px solid black;
}
#root .row .img-container:hover .tooltip {
  display: block;
}
#root .row:nth-child(1) {
  border: 3px solid black;
  background-color: #070811;
  color: white;
}
#root .row:nth-child(1) .img-container:nth-child(3) {
  text-align: right;
  padding-right: 7px;
  -ms-flex-positive: 5;
      flex-grow: 5;
}
#root .row:nth-child(2) {
  border-left: 3px solid black;
  border-right: 3px solid black;
}

@media (max-width: 400px) {
  body {
    font-size: 12px !important;
  }
  body img {
    width: 12px;
  }

  canvas {
    max-width: 300px !important;
  }

  .log {
    max-width: 300px !important;
    font-size: 12px !important;
  }

  .row {
    max-width: 300px !important;
  }
}



